.button-learn-more {
  box-sizing: border-box;
  background-color: var(--bg-card-color-theme-light);
  color: var(--primary-text-color-theme-light);
  letter-spacing: .28px;
  text-transform: uppercase;
  width: 232px;
  height: 48px;
  border: 1px solid #7e8fdd;
  border-radius: 12px;
  margin-right: 13px;
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 20px;
